import React from 'react';
import { Dropdown, DropdownButton } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Header.css';

function Header() {
  return (
    <div className="header">
      <div className="logo">Les Renovateurs</div>
      <nav className="nav">
        <ul>
        
          <li><Link to="/CreateJobber">Rejoindre le rseau</Link></li>
          <li><Link to="/MakeAnAppointment">Prendre un rendez vous</Link></li>
          <li>
            <DropdownButton id="dropdown-basic-button" title="Profil">
              <Dropdown.Item>
                <Link to="/Profil">Settings</Link>
              </Dropdown.Item>
              <Dropdown.Item>
                <Link to="/">Login</Link>
              </Dropdown.Item>
              <Dropdown.Item>
                <Link to="/">Signup</Link>
              </Dropdown.Item>
            </DropdownButton>
          </li>
        </ul>
      </nav>
    </div>
  );
}

export default Header;
